
* {
	margin: 0;
	padding:0;
	font-family: 'Poppins', sans-serif;
	
}

	 .my-container{
		display: flex;
		justify-content: center;
		align-items: center;
		justify-items: center;
		height: 100vh;
		width: 100vw;
		font-family: 'Poppins', sans-serif;
		   
		background-image: url(../src/images/bg-intro-desktop.png);
	   
	} 

	 .container{
		 max-width: 90vmax;
		display: flex;
		justify-content: space-between;
		justify-content: center;
		align-items: center;
		justify-items: center;  
		

	} 

	.left_text_area h1{
		
		font-family: 'Poppins', sans-serif;
		color: white;
		font-weight: 700px;
		max-width: 90%; 
	
	}  

	.left_text_area div{
		
		font-weight: 600px;
		color: white;
		max-width: 90%;  
		margin-bottom: .5rem;
		
	}

	.color-overlay {
		background-color: hsl(0, 100%, 74%) ;
	}	




.my-btn{
	justify-content: space-around;
   display:flex;
   max-width: 100%; 	
   border-radius: 5px;
   background-color: hsl(248, 32%, 49%);
   color: white;
   font-family: 'Poppins', sans-serif;
   font-weight: 600px; 	  
   justify-content: center;		
	  align-items: center;
	justify-items: center;
	   margin-bottom: 1rem;
	 box-shadow: 0px 5px 3px rgba(0, 0, 0, 0.4) ; 	
	padding:1rem;
	


}

	.formarea-box{
		display: grid;
		justify-content: center;
		
		width: 100%;
	
		
	}


	.box-middle{
		
		background-color: white;
		display: grid;
		justify-items: center;
		border-radius: 5px;
	}

	
	
.form-group{
	
	width: 100%; 
	
	background-color:white;
	box-shadow: 0px 3px  rgba(0, 0, 0, 0.4) ;
	border-radius: 5px;


			
			}
			
.form-group input{

				width: 85%;
				box-sizing: border-box;  
				transition: 0.5s;
				outline: none;				
				margin-top: 1.5rem;
				height: 2.5rem;
				border-radius: 5px;
				padding-left: 1rem;
				border-color: rgb(240, 232, 232);
			
				
			  } 
				.form-group [type=text]:focus{
				  border: .1rem solid  hsl(247, 23%, 70%);
			
			} 
			
	

	.my-2ndbtn{
		display: grid;
		width: 85%;
		justify-content: center;	
		align-items: center;
	   box-shadow: 0px 5px 0px #37af7b;
	   border-radius: 5px;
	   background-color:#49D095; 
	   margin-top: 1.5rem;
	   color: white;
	   height: 2rem;
	
	    
	}
	   
	.sa{
		width:85%;
		display: grid;
		font-size:x-small;		  
		justify-content: center;	  
		justify-items: center;
		align-items: center;		
		height: 2rem;
		margin: 1rem;

			}

.btn1{
	color:hsl(246, 25%, 77%);
	}
		
.btn2{
	color: rgb(173, 93, 93)
}
	
.primary{
    font-weight: 700px;
	font-weight: bold;
}

.secondary {
	color:hsl(246, 25%, 77%)

}

	
	.text-muted{
		text-align: center;
	}


	.BoldC{
		font-weight: bold;
		
	}
	  @media only screen and (min-width: 350px) and (max-width: 724px) {
		
	.container {
		max-width: 90%;
		display: grid;
		
		
	
	}


		.left_text_area h1{
			font-family: 'Poppins', sans-serif;
			font-size: 16px;
			color: white;
						font-weight: 700px;
			
			
			
		}  
	
		.left_test_area div{
			font-size: 12px;
			color: white; 
			margin-left: 1.5rem;
		
			
		}
	
		
	
	.my-btn {
		font-size: .8rem;
		height: 2.5rem;
	
		
	}


	

}

